
html, body {
  background: #F5F5F5;
}
#app {
  font-family: "PingFang SC" ,"Helvetica Neue", Helvetica STHeiTi Arial sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
}
.mint-toast.is-placetop {
  padding: 0.13333rem 0.2rem !important;
}
.mint-toast.is-placetop .mint-toast-text {
    font-size: 14px;
}
[data-dpr="2"] .mint-toast.is-placetop .mint-toast-text {
      font-size: 28px;
}
[data-dpr="3"] .mint-toast.is-placetop .mint-toast-text {
      font-size: 42px;
}
.slide-left-enter-active,
.slide-left-leave-active {
  transition: all .1s ease-in;
  opacity: 1;
}
.slide-left-enter,
.slide-left-leave-active {
  opacity: 0;
}



