
.tabar .tab {
  position: fixed;
  bottom: 0;
  left: 0;
  display: flex;
  width: 100%;
  background: #fff;
  box-shadow: 0px 0px 3px rgba(0, 0, 0, 0.2);
}
.tabar .tab .tab-item {
    flex: 1;
    text-align: center;
    height: 1rem;
    width: 2.5rem;
    padding-top: 10px;
}
.tabar .tab .tab-item a {
      display: block;
      font-size: 12px;
      color: #4d555d;
}
[data-dpr="2"] .tabar .tab .tab-item a {
        font-size: 24px;
}
[data-dpr="3"] .tabar .tab .tab-item a {
        font-size: 36px;
}
.tabar .tab .tab-item a.active {
        color: #76D49B;
        text-decoration: none;
}
.tabar .tab .tab-item .tab-icon {
      height: 0.50667rem;
      width: 0.50667rem;
      margin: 0 auto;
}
.tabar .tab .tab-item .tab-icon img {
        width: 100%;
        height: auto;
}
.tabar .tab .tab-item .tab-text {
      padding-top: 0.08rem;
}



